export default {
	data() {
		return {
			myFollows: []
		}
	},
	onLoad() {
		var url = this.$api.urls.getAttentions;
		var data = {};
		this.$api.req.get(url, data, (res) =>{
			console.log(res);
			this.myFollows = res.data.map((item) =>{
				return {
					id: item.id,
					userHeadImg: "http://"+item.imageUrl,
					name: item.name,
					isCertified: item.isCertified
				}
			})
		});
		// this.follow(2);
		// this.follow(27);
		// this.follow(6034);
		// this.follow(534);
	},
	methods: {
		follow(id) {
			var url = this.$api.urls.attentionSomeone;
			var data = {beAttentionUserId: id};
			this.$api.req.post(url, data, (res) =>{
				console.log(res);
			})
		},
		cancelAttention(e) {
			var index = e.currentTarget.dataset.index;
			var url = this.$api.urls.cancelAttention;
			var data = {beAttentionUserId: this.myFollows[index].id};
			this.$api.req.del(url, data, (res) =>{
				console.log(res);
				this.myFollows.splice(e.currentTarget.dataset.index, 1);
				uni.showToast({
					icon: "none",
					title: "取消关注成功"
				})
			})
		}
	}
}
